














































import { getInventoryStatus } from "@/services/status";
import Vue from "vue";
import TableHeader from "../core/TableHeader.vue";
import { TableHeaderModel } from "../models";
export default Vue.extend({
  components: { TableHeader },
  data: () => ({
    search: "",
    isLoading: true,
    headers: [
      {
        text: "عنوان",
        value: "name",
        align: "start",
        sortable: true,
      },
      {
        text: "مقدار",
        value: "value",
        align: "start",
        sortable: true,
      },
      {
        text: "حد هشدار",
        value: "alertLimit",
        align: "start",
        sortable: true,
      },
      {
        text: "نوع مقدار",
        value: "type.name",
        align: "start",
        sortable: true,
      },
      {
        text: "وضعیت",
        value: "status",
        align: "start",
        sortable: true,
      },
      {
        text: "دسته بندی",
        value: "group.name",
        align: "start",
        sortable: true,
      },
      {
        text: "تاریخ ثبت",
        value: "createDate",
        align: "start",
        sortable: true,
      },
      {
        text: "",
        value: "actions",
        align: "center",
        sortable: false,
      },
    ] as Array<TableHeaderModel>,
    inventories: [] as Array<any>,
    page: 1,
    pageCount: 1,
  }),
  mounted() {
    this.loadAlertLimit();
  },
  methods: {
    loadAlertLimit() {
      this.isLoading = false
    },
    getInventoryStatus,
  },
});
